@import url("https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap");
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove text-decoration from a */
a {
  text-decoration: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --dark-blue: hsl(238, 29%, 16%);
  --soft-red: hsl(14, 88%, 65%);
  --background-grad: linear-gradient(hsl(273, 75%, 66%), hsl(240, 73%, 65%));
  --dark-grey-blue: hsl(237, 12%, 33%);
  --grey-blue: hsl(240, 6%, 50%);
  --light-grey-blue: hsl(240, 5%, 91%);
}

.attribution {
  position: fixed;
  font-size: 11px;
  bottom: 0;
  padding: 0.2em 0;
  text-align: center;
  width: 100%;
  background: white;
}

.attribution a {
  color: var(--dark-blue);
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: var(--background-grad);
  font-size: 14px;
  font-family: "Kumbh Sans", sans-serif;
}

.main {
  margin-top: -6em;
}

.container {
  position: relative;
  width: 90%;
  max-width: 1000px;
  margin: 0 auto;
  background: white;
  padding: 2em;
  padding-top: 10em;
  border-radius: 20px;
}

.vector-container {
  max-width: 100%;
  margin: auto auto;
  text-align: center;
}
.vector-container .vector-container-mobile .vector-mobile {
  position: relative;
  bottom: -7em;
  z-index: 11;
  filter: drop-shadow(0px 30px 2px rgba(0, 0, 0, 0.1));
}
.vector-container .vector-container-desktop {
  display: none;
}

.accordion-container h2 {
  font-size: 2rem;
  text-align: center;
  color: var(--dark-blue);
  font-weight: 700;
}
.accordion-container .accordion {
  border-bottom: 1px solid var(--light-grey-blue);
  padding-block: 0.8em;
}
.accordion-container .accordion .summary {
  background: white;
  border: none;
  width: 100%;
  text-align: start;
  color: var(--dark-grey-blue);
  padding-inline: 0;
  padding-block: 0.8em;
  cursor: pointer;
}
.accordion-container .accordion .summary::after {
  content: url('data:image/svg+xml;charset=UTF-8, <svg width="10" height="7" xmlns="http://www.w3.org/2000/svg"><path d="M1 .799l4 4 4-4" stroke="#F47B56" stroke-width="2" fill="white" fill-rule="evenodd"/></svg>');
  transition: all 0.25s;
  float: right;
}
.accordion-container .accordion .summary:hover {
  color: var(--soft-red);
}
.accordion-container .accordion .active {
  color: var(--dark-blue);
  font-weight: 700;
  transition: all 0.25s;
}
.accordion-container .accordion .active:hover {
  color: var(--dark-blue);
}
.accordion-container .accordion .active::after {
  transform: rotate(180deg);
  transition: all 0.25s;
}
.accordion-container .accordion .detail {
  background: white;
  color: var(--grey-blue);
  max-height: 0;
  overflow: hidden;
  transition: all 0.25s;
}

@media (min-width: 900px) {
  body {
    align-items: center;
    justify-content: center;
  }

  .main {
    display: grid;
    grid-auto-flow: column;
    margin: 0;
  }

  .vector-container {
    margin: 0;
    padding: 0;
    inset: 0;
    position: absolute;
    z-index: 111;
    top: 25%;
    left: -25%;
  }
  .vector-container .vector-container-mobile {
    display: none;
    inset: 0;
  }
  .vector-container .vector-container-desktop {
    display: inline-block;
  }
  .vector-container .vector-container-desktop .vector-desktop {
    display: inline-block;
  }
  .vector-container .vector-container-desktop .vector-box {
    position: absolute;
    z-index: 222;
    margin-left: -38%;
    margin-top: 12%;
  }

  .container {
    padding: 0;
    margin: 0;
    inset: 0;
    padding: 2em;
    padding-left: 20em;
    justify-self: end;
    margin-right: -8%;
  }

  .attribution {
    position: fixed;
    font-size: 11px;
    bottom: 0;
    padding: 0.2em 0;
    text-align: center;
    width: 100%;
    background: white;
  }

  .attribution a {
    color: var(--dark-blue);
  }
}